blockquote, body, dd, div, dl, dt, fieldset, form, h1, h2, h3, h4, h5, h6, input, li, ol, p, pre, td, textarea, th, ul {
    padding: 0;
    margin: 0
}

a {
    text-decoration: none
}

table {
    border-spacing: 0
}

fieldset, img {
    border: 0
}

address, caption, cite, code, dfn, em, strong, th, var {
    font-weight: 400;
    font-style: normal
}

strong {
    font-weight: 700
}

ol, ul {
    list-style: none;
    margin: 0;
    padding: 0
}

caption, th {
    text-align: left
}

h1, h2, h3, h4, h5, h6 {
    font-weight: 400;
    font-size: 100%;
    margin: 0;
    padding: 0
}

q:after, q:before {
    content: ''
}

abbr, acronym {
    border: 0
}

* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box
}

body {
    font-family: arial, helvetica, serif
}

#app, #fullpage, .section, body, html {
    height: 100%
}

h3 {
    font-size: 5em;
    text-align: center;
    color: #fff;
    font-weight: 700
}

#logo {
    position: fixed;
    top: 20px;
    left: 20px;
    color: #fff;
    font-weight: 700;
    z-index: 99;
    font-size: 2em;
    -webkit-font-smoothing: antialiased;
    -moz-font-smoothing: antialiased
}

#menu-line {
    position: absolute;
    bottom: -4px;
    left: 0;
    width: 159px;
    height: 2px;
    background: #fff
}

#menu {
    position: fixed;
    top: 20px;
    right: 20px;
    z-index: 70;
    -webkit-font-smoothing: antialiased;
    -moz-font-smoothing: antialiased;
    letter-spacing: 1px;
    font-size: 1.6em;
}

#menu li {
    display: inline-block;
    margin: 10px 0;
    position: relative;
}

#menu a {
    cursor: pointer;
    color: #fff;
    padding: 0 1.1em 1.1em 1.1em
}

#menu a:hover {
    color: #282c34;
}

#menu li.active a {
    color: #282c34;
}

#menu li.active a:after {
    content: '';
    margin: 0 1.1em 0 1.1em;
    height: 2px;
    background: #282c34;
    display: block;
    position: absolute;
    bottom: -6px;
    left: 0;
    right: 0;
}

.custom-section {
    color: #fff;
    font-weight: 700;
    font-size: 3em;
    -webkit-font-smoothing: antialiased;
    -moz-font-smoothing: antialiased;
}


@media screen and (max-width: 1135px) {

}

@media screen and (max-width: 1050px), screen and (max-height: 600px) {
    #menu {
        font-size: 1.4em;
    }

    #logo {
        font-size: 1.7em;
    }
}

@media screen and (max-width: 1030px) {

}

@media screen and (max-width: 900px), screen and (max-height: 500px) {
    #menu {
        font-size: 1.1em;
    }

    #logo {
        font-size: 1.5em;
    }
}

@media screen and (max-width: 930px) {

}

@media screen and (max-width: 750px) {
    #menu {
        font-size: 1em;
    }

    #logo {
        font-size: 1.2em;
    }
}

@media screen and (max-width: 700px), screen and (max-height: 400px) {

}

@media screen and (max-width: 630px) {

    #menu {
        letter-spacing: 0;
        font-size: .95em;
        font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol'
    }

    #logo {
        display: none;
    }
}

@media screen and (max-width: 550px) {
    #menu li a {
        padding: 0 .7em .7em .7em
    }

    #section-1 h2:before {
        height: 64px;
        width: 75px
    }
}

@media screen and (max-width: 380px) {

}
